@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
:root{
  font-size: 62.4%;
  scroll-behavior: smooth;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img{
  width: 100%;
  object-fit: cover;
}
h1{
  font-size: clamp(4rem, 3.5vw, 6rem);
  font-weight: bold;
  letter-spacing: .2rem;
}
h2{
  font-size: clamp(3rem, 2.5vw, 3.5rem);
  letter-spacing: .2rem;
}
h3{
  font-size: clamp(2rem, 1.5vw, 2rem);
}
p{
  font-size: clamp(1.5rem, 1.5vw, 2rem);
  letter-spacing: .1rem;
  line-height: 1.5;
}
a{
  text-decoration: none;
  font-size: clamp(1.5rem, 1vw, 1.6rem);
  font-weight: 500;
  transition: color linear .3s, background-color linear .3s;
}
