main{
    width: 100%;
    height: 65vh;
}

main .main-section-landing{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 3rem;
    gap: 3rem;
    background-color: #111;
    position: relative;
    z-index: 2;
}
.main-section-landing video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: .8;
}


.main-section-landing .main-section-myInfo, 
.main-section-landing .main-section-buttons{
    width: 80%;
    display: flex;
    z-index: 999;
}
.main-section-landing .main-section-myInfo{
    flex-direction: column;
    grid-gap: .5rem;
    gap: .5rem;
}
.main-section-buttons{
    grid-gap: 1.5rem;
    gap: 1.5rem;
}


.main-section-myInfo h1, .main-section-myInfo h2{
    color: #fff;
}

.main-section-buttons a{
    border: none;
    padding: 1.2rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    font-size: 2rem;
    border-radius: 5px;
    color: #fff;
    box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.3);
    transition: color ease-in-out .3s, background-color ease-in-out .3s;
    outline: none;
}
.main-section-buttons a:hover{
    cursor: pointer;
}
.main-section-buttons a:active{
    opacity: .9;
    box-shadow: 4px 4px 10px 0 rgba(0, 0, 0, 0.4);
}

.main-section-buttons #btn-curriculo{
    background-color: #2e86de;
}
.main-section-buttons #btn-curriculo:hover{
    background-color: #2874c0;
    
}
.main-section-buttons #btn-projetos,
.main-section-buttons #btn-github
{
    background-color: #01a3a4;
}
.main-section-buttons #btn-projetos:hover,
.main-section-buttons #btn-github:hover
{
    background-color: #008f8f;
}

.main-section-about{
    width: 80%;
    max-width: 1100px;
    margin: 10rem auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 10rem;
    gap: 10rem;
}
.main-section-about p{
    font-weight: bold;
}
.main-section-about .image-container{
    max-width: 40%;
}


@media only screen and (max-width: 768px){
    .main-section-landing .main-section-myInfo{
        text-align: center;
    }
    .main-section-buttons{
        flex-direction: column;
        text-align: center;
    }

    .main-section-buttons button{
        font-size: 1.5rem;
    }
    .main-section-about{
        flex-direction: column;
        grid-gap: 5rem;
        gap: 5rem;
    }
    .main-section-about .image-container{
        max-width: 50%;
    }
}
.section-projects{
    width: 80%;
    max-width: 1100px;
    margin: 5rem auto;
    display: flex;
    flex-direction: column;
    
}

.section-projects header{
    margin-bottom: 5rem;
}
.section-projects .projects-container{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}

.section-projects .project{
    width: 30%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow: hidden;
    box-shadow: 0px 0px 5px 0 rgba(0, 0, 0, 0.2);
    transition: transform linear .3s, box-shadow ease-in-out .4s;
}


.section-projects .project:hover{
    transform: translateY(-15px);
    box-shadow: 0px 0px 15px 0 rgba(0, 0, 0, 0.3);

}
.section-projects .project .image-container{
    width: 100%;
}
.section-projects  .project .info-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
}
.project .info-container a{
    color: #fff;
    padding: 1rem;
    background-color:  #01a3a4;
    border-radius: 5px;
    text-align: center;
}
.project .info-container a:hover{
    background-color: #00bebe;
}

@media only screen and (max-width: 768px){
    .section-projects .project{
        width: 100%;
    }
}
.section-github{
    width: 80%;
    max-width: 1100px;
    margin: 10rem auto;
    display: flex;
    flex-direction: column;
    
}

.section-github header{
    margin-bottom: 5rem;
}

.section-github .projects-container{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    grid-gap: 2rem;
    gap: 2rem;
}
.section-github .project{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
    grid-gap: 2rem;
    gap: 2rem;
    padding: 2rem;
    box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.2);
    transition: transform linear .3s, box-shadow ease-in-out .2s;
}
.section-github .project:hover{
    transform: translateX(-15px);
    box-shadow: 0px 0px 15px 0 rgba(0, 0, 0, 0.3);

}

.section-github .project a{
    color: #fff;
    padding: 1rem;
    background-color:  #2e86de;
    border-radius: 5px;
    text-align: center;
}
.section-github .project a:hover{
    background-color: #2874c0;
}
.section-github .project p.no-desc{
    opacity: .7;
}
footer{
    width: 100%;
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
}
.footer-text p{
    display: flex;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    color: #fff;
}
